export default {
	name:"Speaker",
	props: {
	 list : {
      type  : Array,
      default : function(){return [];}
     },
    iconClass : {
      type  : String,
      default : ""
    },
	iconColor : {
	  type  : String,
	  default : "#3688FF"
	},
    interval : {
      type : Number,
      default: 3000
    },
    vertical : {
     type : Boolean,
     default : true
     },
	 scrolling:{
		type : Boolean,
		default :false 
	 }
	}
}
